import {appendAddress} from './Address';
import {attachAddressToDropinCheckout} from '../checkout-sessions-api';
import {validateClientDomElement, waitForRequiredDataOnAddress} from '../utils';
import {billingAddressOnlyMode, getBillingCountry, getShippingCountry} from './address-utils';

export function defaultError() {
  return {
    shippingAddress: {},
    billingAddress: {},
    main: ''
  };
}

// Comes from: https://github.digitalriverws.net/EnterpriseAdmin/hydra/blob/8da7d8341599d0831bf7b1eadd6de78c99bd4007/common-client/src/utils/email.ts
// eslint-disable-next-line no-useless-escape
const EMAIL_REGEX = /^(([^<>()\[\]\\.,;:\s@']+(\.[^<>()\[\]\\.,;:\s@']+)*)|('.+'))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

function getBillingAddressSchemaProperties(billingAddressSchemaRaw) {
  const properties = {
    email: {type: 'string', description: 'persons email', minLength: 0, maxLength: 128, pattern: EMAIL_REGEX}, // TODO EMAIL PATTERN // TODO REMOVE WHEN COMING FROM API
    phone: {type: 'string', description: 'persons email', minLength: 0, maxLength: 128},
    firstName: {type: 'string', description: 'persons first name', minLength: 0, maxLength: 128},
    lastName: {type: 'string', description: 'persons last name', minLength: 0, maxLength: 128},
    ...JSON.parse(billingAddressSchemaRaw).properties
  };

  delete properties.name;

  return properties;
}

function getShippingAddressSchemaProperties(shippingAddressSchemaRaw) {
  const properties = { // EMAIL PATTERN: /^(([^<>()\[\]\\.,;:\s@']+(\.[^<>()\[\]\\.,;:\s@']+)*)|('.+'))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    email: {type: 'string', description: 'persons email', minLength: 0, maxLength: 128, pattern: EMAIL_REGEX}, // TODO EMAIL PATTERN // TODO REMOVE WHEN COMING FROM API
    phone: {type: 'string', description: 'persons email', minLength: 0, maxLength: 128},
    firstName: {type: 'string', description: 'persons first name', minLength: 0, maxLength: 128},
    lastName: {type: 'string', description: 'persons last name', minLength: 0, maxLength: 128},
    ...JSON.parse(shippingAddressSchemaRaw).properties
  };

  delete properties.name;

  return properties;
}

function validateInputAgainstSchema(type, properties, requiredFields, storage, errors) {
  Object.keys(properties).forEach(key => {

    const isRequired = requiredFields.includes(key);
    const value = storage?.[type]?.[key];
    const pattern = properties[key].pattern;

    if (isRequired && (typeof value === 'undefined' || value.length === 0)) {
      errors[type][key] = {
        code: 'LENGTH',
        message: ''
      };
    } else if (typeof pattern !== 'undefined' && !new RegExp(pattern).test(value)) {
      errors[type][key] = {
        code: 'PATTERN',
        message: storage.translations.error.validationError
      };
    }

  });
}

function getBillingRequiredFields(billingAddressSchemaRaw, checkoutData, shippingAddressTypeSameAsBilling) {
  const billingRequiredFields = JSON.parse(billingAddressSchemaRaw).required;

  billingRequiredFields.push( // TODO REMOVE WHEN COMING FROM API
    'firstName',
    'lastName'
  );

  if(checkoutData?.options?.requiredFields && (checkoutData.options.requiredFields).indexOf('billTo.phone') > -1){
    billingRequiredFields.push(
      'phone',
    );
  }

  if(checkoutData.customerType === 'business'){
    billingRequiredFields.push('organization');
  }

  if (useBillingAddress(checkoutData, shippingAddressTypeSameAsBilling)) {
    billingRequiredFields.push('email');
  }

  return billingRequiredFields;
}

function getShippingRequiredFields(shippingAddressSchemaRaw, checkoutData, shippingAddressTypeSameAsBilling) {
  const shippingRequiredFields = JSON.parse(shippingAddressSchemaRaw).required;

  shippingRequiredFields.push( // TODO REMOVE WHEN COMING FROM API
    // 'phone',
    'firstName',
    'lastName'
  );

  if(checkoutData?.options?.requiredFields && (checkoutData.options.requiredFields).indexOf('shipTo.phone') > -1){
    shippingRequiredFields.push(
      'phone',
    );
  }

  if(checkoutData.customerType === 'business'){
    shippingRequiredFields.push('organization');
  }

  if (!useBillingAddress(checkoutData, shippingAddressTypeSameAsBilling)) {
    shippingRequiredFields.push('email');
  }

  return shippingRequiredFields;
}


function addressWithValidProperties(address) {
  const notAValidAddressAPIProperty = ['email', 'firstName', 'lastName', 'phone', 'name', 'address', 'organization'];

  const newAddress = Object.assign({}, address);
  notAValidAddressAPIProperty.forEach( i => delete newAddress[i] );

  return newAddress;
}

function setValueWhenOnlySingleEnumValue(schema, storage, addressType) {
  Object.keys(schema).forEach(key => {
    const property = schema[key];
    if (typeof property.enum !== 'undefined' && property.enum.length === 1) {
      storage[addressType][key] = property.enum[0].countryCode;
    }
  });
}

const BILLING_ADDRESS_TYPE = 'billingAddress';
const SHIPPING_ADDRESS_TYPE = 'shippingAddress';

function setErrorMessageAndRemount(storage, callApiAndMount, errorMessage) {
  callApiAndMount(storage.checkoutData, {
    ...defaultError(),
    main: errorMessage
  });
}

function useBillingAddress(checkoutData, shippingAddressTypeSameAsBilling) {

  if (billingAddressOnlyMode(checkoutData)) {
    return true;
  }

  if (!shippingAddressTypeSameAsBilling) {
    return true;
  }

  return false;

}

function getBillingAddressToUse(checkoutData, storage) {
  if (useBillingAddress(checkoutData, storage.shippingAddressTypeSameAsBilling)) {
    return storage.billingAddress;
  }

  return storage.shippingAddress;
}

const getFieldsByCountry = (combinedFields, countryFieldByCountry) => {
  Object.keys(combinedFields).forEach((key) => {
    if (key in combinedFields && key in countryFieldByCountry) {
      Object.keys(countryFieldByCountry[key]).forEach((attr) => {
        combinedFields[key][attr] = countryFieldByCountry[key][attr];
      });
    }
    if(key === 'email'){
      combinedFields['email']['pattern'] = EMAIL_REGEX;
    }
  });
  return combinedFields;
};

const getOrderFields = (splitAddressFormat) => {
  let orderFields = splitAddressFormat.map(str => {
    const regex = /\{(.+?)\}/g; 
    const matches = [];
    let match;

    while ((match = regex.exec(str)) !== null) {
      matches.push(match[1]);
    }

    switch (matches.length) {
      case 1:
        return matches[0];
      default:
        return matches;
    }
  });
  return orderFields;
};

const getJsonSchemaByCountry = (country, addressType, checkoutData, storage) =>{
  addressType = billingAddressOnlyMode(checkoutData) ? 'billingOnly' : addressType;
  let defaultSchema = {
    '$schema':'http://json-schema.org/draft-07/schema#',
    'title':'Address',
    'type':'object',
    'properties':{},
    'required':[]
  };
  let countryFields = (checkoutData.shoppingCountry && storage.jsonSchematranslations.countries[checkoutData.shoppingCountry] && country === '')? storage.jsonSchematranslations.countries[checkoutData.shoppingCountry] : storage.jsonSchematranslations.countries[country ? country : 'US'];
  let addressFormat = ((storage.isCustomerTypeBusiness || checkoutData.customerType === 'business') && countryFields.addresses) ? countryFields.addresses[addressType].format.business : countryFields.addresses[addressType].format.individual;
  let countryFieldByCountry = countryFields.fields;
  let combinedFields = JSON.parse(JSON.stringify(storage.jsonSchematranslations.defaultFields));
  combinedFields = getFieldsByCountry(combinedFields, countryFieldByCountry);

  const regex = /_/g;
  const splitAddressFormat = addressFormat.split(regex);
  let orderedField = getOrderFields(splitAddressFormat);
  let customerType = (checkoutData.customerType === 'business' || storage.isCustomerTypeBusiness) ? 'business' : 'individual';
  let requiredFields = countryFields.addresses[addressType].requiredFields[customerType];
  requiredFields = requiredFields.filter(field => field !== 'email');
  defaultSchema['required'] = requiredFields;
  defaultSchema['properties'] = combinedFields;
  defaultSchema['properties']['country']['enum'] = (checkoutData.shoppingCountry && (storage.jsonSchematranslations.countries[checkoutData.shoppingCountry])  && (addressType === 'shipping'))?  new Array(storage.jsonSchematranslations.countries[checkoutData.shoppingCountry]) : Object.values(storage.jsonSchematranslations.countries);
  defaultSchema['orderedField'] = orderedField;
  return defaultSchema;
};

export function address(key, configuration, storage) {

  let nodeToKeep;

  storage.shippingAddress = {};
  storage.billingAddress = {};
  storage.billingOnly = {};
  storage.shippingAddressTypeSameAsBilling = true;
  storage.isCustomerTypeBusiness = false;
  storage.isEmailPromotionsOptIn = false;
  storage.isAllowBusinessPurchases = false;
  storage.isEmailPromotionsOptInDisplay = false;

  const callApiAndMount = async function (checkoutData, errors) {
    const billingAddressSchemaRaw = getJsonSchemaByCountry(getBillingCountry(checkoutData, storage), 'billing', checkoutData, storage);
    const shippingAddressSchemaRaw = getJsonSchemaByCountry(getShippingCountry(storage), 'shipping', checkoutData, storage);

    const billingAddressSchemaProperties = getBillingAddressSchemaProperties(JSON.stringify(billingAddressSchemaRaw));
    const shippingAddressSchemaProperties = getShippingAddressSchemaProperties(JSON.stringify(shippingAddressSchemaRaw));

    const shippingRequiredFields = getShippingRequiredFields(JSON.stringify(shippingAddressSchemaRaw), checkoutData, storage.shippingAddressTypeSameAsBilling);
    const billingRequiredFields = getBillingRequiredFields(JSON.stringify(billingAddressSchemaRaw), checkoutData, storage.shippingAddressTypeSameAsBilling);

    const isEURowMessageDisplay = (checkoutData.billTo && checkoutData.billTo.address && checkoutData.billTo.address.country !== 'US') ||
      (checkoutData.shipTo && checkoutData.shipTo.address && checkoutData.shipTo.address.country !== 'US');
    const isAllowBusinessPurchases = checkoutData.options && checkoutData.options.customOptions && checkoutData.options.customOptions.allowBusinessPurchases;

    appendAddress(nodeToKeep, {
      errors: typeof errors === 'undefined' ? defaultError() : errors,
      billingAddressOnlyMode: billingAddressOnlyMode(checkoutData),
      useBillingAddress: useBillingAddress(checkoutData, storage.shippingAddressTypeSameAsBilling),
      savedAddresses: checkoutData?.options?.addresses,
      translations: storage.translations,
      emailAddressOrder: ['email'],
      emailRequiredField : ['email'],
      shippingAddressSchema: shippingAddressSchemaProperties,
      billingAddressSchema: billingAddressSchemaProperties,
      shippingRequiredFields: shippingRequiredFields,
      billingRequiredFields: billingRequiredFields,
      shippingAddressOrder: shippingAddressSchemaRaw.orderedField,
      billingAddressOrder: billingAddressSchemaRaw.orderedField,
      taxInclusive: checkoutData.taxInclusive,
      isEURowMessageDisplay: isEURowMessageDisplay,
      isShippingAddressSameAsBilling: storage.shippingAddressTypeSameAsBilling,
      isHaveCustomerId: checkoutData.customerId,
      shoppingCountry: checkoutData.shoppingCountry ? checkoutData.shoppingCountry : '',
      isCustomerTypeBusiness : storage.isCustomerTypeBusiness,
      isEmailPromotionsOptIn : storage.isEmailPromotionsOptIn ? storage.isEmailPromotionsOptIn : false,
      isEmailPromotionsOptInDisplay : checkoutData?.options?.consents?.emailPromotions?.enabled ? true : false,
      isCustomerTypeBusinessFromCheckoutData : checkoutData.customerType === 'business',
      isAllowBusinessPurchases: isAllowBusinessPurchases,
      updateAddress: function (addressType, address) {
        storage[addressType] = address;
      },
      getAddress: function (addressType) {
        return storage[addressType];
      },
      handleShippingAddressTypeChange: function (selected) {
        storage.shippingAddressTypeSameAsBilling = selected;
        callApiAndMount(checkoutData);
      },
      handleCustomerTypeChange: function (selected) {
        storage.isCustomerTypeBusiness = selected;
        callApiAndMount(checkoutData);
      },
      handleEmailPromotionsOptInChange: function (checked) {
        storage.isEmailPromotionsOptIn = checked;
        callApiAndMount(checkoutData);
      },
      updateErrors: function (errors) {
        callApiAndMount(checkoutData, errors);
      },
      updateAddressAndErrors: function (addressType, address, errors) {
        storage[addressType] = address;
        let countrySelected = address.country;
        if(billingAddressOnlyMode(checkoutData)){
          storage['billingOnly']['selectedCountry'] = countrySelected ;
        } else{
          storage[addressType]['selectedCountry'] = countrySelected ;
        }
        callApiAndMount(checkoutData, errors);
      }
    });

  };

  return {
    mount: (node) => {

      nodeToKeep = validateClientDomElement(node);

      storage.subscribers.push(callApiAndMount);

      waitForRequiredDataOnAddress(storage).then( () => {
        callApiAndMount(storage.checkoutData);
      });

    },
    done : async function () {

      const errors = defaultError();

      const checkoutData = storage.checkoutData;

      setErrorMessageAndRemount(storage, callApiAndMount, '');

      const billingAddressSchemaRaw = getJsonSchemaByCountry(getBillingCountry(checkoutData, storage), 'billing', checkoutData, storage);
      const shippingAddressSchemaRaw = getJsonSchemaByCountry(getShippingCountry(storage), 'shipping', checkoutData, storage);
      const billingRequiredFields = getBillingRequiredFields(JSON.stringify(billingAddressSchemaRaw), checkoutData, storage.shippingAddressTypeSameAsBilling);
      const shippingRequiredFields = getShippingRequiredFields(JSON.stringify(shippingAddressSchemaRaw), checkoutData, storage.shippingAddressTypeSameAsBilling);

      const billingAddressProperties = getBillingAddressSchemaProperties(JSON.stringify(billingAddressSchemaRaw));
      const shippingAddressProperties = getShippingAddressSchemaProperties(JSON.stringify(shippingAddressSchemaRaw));

      if (checkoutData.requiresShipping === true) {
        setValueWhenOnlySingleEnumValue(shippingAddressProperties, storage, SHIPPING_ADDRESS_TYPE);
        validateInputAgainstSchema(SHIPPING_ADDRESS_TYPE, shippingAddressProperties, shippingRequiredFields, storage, errors);
      }

      if (checkoutData.requiresShipping === false || storage.shippingAddressTypeSameAsBilling === false) {
        setValueWhenOnlySingleEnumValue(billingAddressProperties, storage, BILLING_ADDRESS_TYPE);
        let billingCountryPreSelected = (checkoutData.shoppingCountry && !storage[BILLING_ADDRESS_TYPE]['selectedCountry'])? checkoutData.shoppingCountry: '';
        if(billingCountryPreSelected){
          storage[BILLING_ADDRESS_TYPE] = {...storage[BILLING_ADDRESS_TYPE], country : billingCountryPreSelected };
        }
        validateInputAgainstSchema(BILLING_ADDRESS_TYPE, billingAddressProperties, billingRequiredFields, storage, errors);
      }

      // If not valid, then notify UI
      if (Object.keys(errors.shippingAddress).length > 0 || Object.keys(errors.billingAddress).length > 0) {
        callApiAndMount(storage.checkoutData, errors);
        return false;
      }

      const billingAddressToUse = getBillingAddressToUse(checkoutData, storage);

      const billingAddressFromState = addressWithValidProperties(billingAddressToUse);
      const shippingAddressFromState = addressWithValidProperties(storage.shippingAddress);

      const address = {
        'email': billingAddressToUse.email,
        'billTo': {
          'address': billingAddressFromState,
          'organization' : shippingAddressFromState.organization,
          'email': billingAddressToUse.email,
          'name': billingAddressToUse.firstName + ' ' + billingAddressToUse.lastName,
          'phone': billingAddressToUse.phone
        },
        'shipTo': {
          'address': shippingAddressFromState,
          'organization' : shippingAddressFromState.organization,
          'email': billingAddressToUse.email,
          'name': storage.shippingAddress.firstName + ' ' + storage.shippingAddress.lastName,
          'phone': storage.shippingAddress.phone
        },
        'consents' :{
          emailPromotionsOptIn: checkoutData?.options?.consents?.emailPromotions?.enabled ? true : false
        }
      };

      delete address.shipTo.address.selectedCountry;
      delete address.billTo.address.selectedCountry;

      if (checkoutData.requiresShipping === false) {
        delete address.shipTo;
      }

      try {

        const response = await attachAddressToDropinCheckout(address, configuration.checkoutSessionId, storage.fingerprint, storage.apiKey);
        if (response.status !== 200) {
          setErrorMessageAndRemount(storage, callApiAndMount, storage.translations.error.notSaved);
          return false;
        }
        storage.notify();
        return true;

      } catch (e) {
        setErrorMessageAndRemount(storage, callApiAndMount, storage.translations.error.notSaved);
        return false;
      }

    }

  };

}